body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Onboarding-1 */

.onbarding-1{
background: linear-gradient(336.91deg, #F0F4FD -56.86%, #FFFFFF 126.39%);
}

.ellipse{
position: absolute;
left: 15%;
right: 85.6%;
top: 4.93%;
bottom: 85.84%;
background: linear-gradient(147.47deg, #FEB665 24.48%, #F66EB4 118.65%);
border-radius: 50%;
height:50px;
width:50px;
}


.ellipse-2{
position: absolute;
left: 81.6%;
right: 0%;
top: 4.93%;
bottom: 85.84%;
background: linear-gradient(228.76deg, #2DEEF9 -34.42%, #E084F1 127.18%);
-webkit-transform: rotate(-15.82deg);
        transform: rotate(-15.82deg);
border-radius: 50%;
height:50px;
width:50px;
}

.book-logo{

    position: absolute;
    width: 212px;
    height: 212px;
    left: 0;
    top: 8%;
    background: linear-gradient(73.12deg, #25CED1 -19.51%, #34E6E9 58.02%, #6AFCFF 138%);
    right: 0;
    margin-left: auto;
    margin-right: auto;
    border-radius: 50%;
}

.vector{
    position: absolute;
    left: 0px;
    right: 0;
    top: 6.25%;
    bottom: 6.25%;
    background: linear-gradient(180deg, whitesmoke 0%, whitesmoke 100%);
    width: 80px;
    margin: auto;
    height: 62px;
    
}


